"use client"

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Link } from "@nextui-org/link";
import { button as buttonStyles } from "@nextui-org/theme";
import { siteConfig } from "@/config/site";

const Home = () => {
  const [loading, setLoading] = useState(true); // State to track if we're loading
  const [isClient, setIsClient] = useState(false); // Check if running on client
  const router = useRouter();

  // Check if running in client
  useEffect(() => {
    setIsClient(true);
  }, []);

  const checkAuthToken = () => {
    const token = localStorage.getItem("token"); // Or get token from cookies
    if (!token) {
      // If no token, redirect to login page
      router.push("/login");
    } else {
      try {
        // Check if token is valid by sending a request to your backend to verify the token
        fetch("/api/auth/verify-token", {
          method: "GET",
          headers: {
            "Authorization": `Bearer ${token}`
          },
        })
        .then(response => {
          if (!response.ok) {
            router.push("/login"); // Redirect if the token is invalid or expired
          }
        })
        .catch(() => {
          router.push("/login"); // Redirect in case of error (e.g., token expired)
        });
      } catch (error) {
        router.push("/login");
      }
    }
  };

  useEffect(() => {
    if (isClient) {
      setLoading(true);
      checkAuthToken(); // Run on page load to verify token before rendering buttons
      setLoading(false);
    }
  }, [isClient]);

  if (loading) {
    return <div>Loading...</div>; // Show loading until token is checked
  }

  return (
    <section className="min-h-screen flex flex-col items-center justify-center gap-4 md:py-10">
      <div className="inline-block max-w-xl text-center justify-center">
        <span className="text-4xl text-violet-500">Basobas&nbsp;</span>
        <div className="mt-4 text-lg text-gray-600">
          adipisicing elit. Accusamus ratione inventore dolore
        </div>
      </div>

      <div className="flex gap-2">
        <Link
          className={buttonStyles({
            color: "secondary",
            radius: "full",
            variant: "shadow",
            size: "md",
          })}
          href={siteConfig.links.find}
        >
          Find
        </Link>
        <Link
          className={buttonStyles({
            color: "secondary",
            radius: "full",
            variant: "shadow",
            size: "md",
          })}
          href={siteConfig.links.list}
        >
          List
        </Link>
      </div>
    </section>
  );
};

export default Home;
